.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.title {
  font-size: 28px;
  margin-bottom: 20px;
}

.upload-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.previews {
  display: flex;
  justify-content: center;
  align-items: center;
}

.preview {
  margin-right: 20px;
  text-align: center;
}

.preview-image {
  object-fit: cover;
  border-radius: 8px;
}

.upload-button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 18px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.generate-button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 18px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-left: 20px;
}

.generate-button:disabled {
  background-color: #ccc; /* Grey color when disabled */
  cursor: not-allowed; /* Show disabled cursor */
}

.upload-button:hover {
  background-color: #0056b3;
}

.error {
  color: red;
  margin-top: 5px;
}
